import React, { Component } from 'react'
import Editor from 'draft-js-plugins-editor'
import createMathjaxPlugin from './draft-js-mathjax-plugin/src'
// import createMathjaxPlugin from 'draft-js-mathjax-plugin'
import { EditorState, convertFromRaw, convertToRaw } from 'draft-js'
import rawContent from './data.json'
const content = convertFromRaw(rawContent)

const MathjaxPlugin = createMathjaxPlugin()

const plugins = [MathjaxPlugin]
// const plugins = []

const styles = {
  editor: {
    boxSizing: 'border-box',
    textAlign: 'left',
    fontFamily: '\'Open Sans\', sans-serif',
    minHeight: 200,
    width: '50%',
    padding: 16,
    border: '1px solid #ddd',
    marginLeft: 'auto',
    marginRight: 'auto',
    borderRadius: 5,
    boxShadow: 'inset 0px 1px 8px -3px #ABABAB',
    background: '#fefefe'
  },
  title: {
    textAlign: 'center',
  }
}

class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      editorState: EditorState.createWithContent(content),
    }

    this.focus = () => {
      this.editor.focus()
    }

    // this.getContent = () => {
    //   const content = this.state.editorState.getCurrentContent()
    //   console.log(JSON.stringify(convertToRaw(content), null, 2))
    // }
  }

  // componentDidMount() {
  //   this.editor.focus()
  // }

  onChange = (editorState) => {
    this.setState({
      editorState,
    })
  }

  render() {

    // <button onClick={this.getContent}>content</button>
    return (
      <div style={styles.title}>
        <h2>Demo for the draft-js-mathjax-plugin</h2>
          <p>Use <code>$</code> key to insert inline math and <code>CTRL+M</code> for block one.</p>
        <div style={styles.editor} onClick={this.focus}>
          <Editor
            editorState={this.state.editorState}
            onChange={this.onChange}
            plugins={plugins}
            ref={(editor) => {this.editor = editor}}
          />
        </div>
      </div>
    )
  }
}

export default App



